.wines{
    display:flex;
    justify-content: center;
    align-items: center;
    margin-top:10vh;
    width:100%;
    flex-direction: column;
}

.wines h1{
    padding: 20px;
}
.grid-container{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(300px,1fr));
     grid-gap: 50px;
    width:80%;
    justify-items: center;
    align-self: center;
    align-items: center;
}
.grid-container div{
    position: relative;
    display:flex;
    align-items: center;
    justify-content: end;
    margin:30px;
    flex-direction: column;
    background-image: url(./slika-boca.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size:contain;
    width: 300px;
    height: 300px;
    overflow:hidden;
    box-shadow: 5px 5px 15px 0px rgba(128, 128, 128,0.6);
    transition:all 200ms ease-in;
    cursor:pointer;
    

  
}

.grid-container p{
    position: absolute;
    bottom:0;
    background-color:rgba(0, 0, 0, 0.7);
    width:100%;
    text-align: center;
    height: 60px;
    margin: 0;
    padding-top: 20px;
    font-size: 20px;
    opacity:0;
    transform: translateY(100%);
    transition:all 200ms ease-in-out;
    color:white;

}
.grid-container div:hover{
    transform: scale(1.1);
    box-shadow: 5px 5px 25px 0px rgba(128, 128, 128,0.6);
}
.grid-container div:hover>p{
    transform: translateY(0%);
    opacity: 100;
}